@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100..900;1,100..900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap');

.body_wrapper {
  width: 100%;
  max-width: 1260px;
  padding: 0 20px;
  margin: 0 auto;
  box-sizing: border-box;
}

html {
  overflow-x: hidden;
}

body {
  margin: 0;
  font-family: "Jost", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  line-height: 150%;
  overflow-x: hidden;
  box-sizing: border-box;
  word-break: break-word;
}

a {
  font-size: 16px;
  color: #222326;
  text-decoration: none;
}

h1 {
  font-size: 3.11rem;
  line-height: 1.14;
  font-family: Jost, sans-serif;
  font-style: normal;
  font-weight: 600;
  margin: 0 0 24px 0;
}

h2 {
  font-size: 2.24rem;
  font-family: Jost, sans-serif;
  font-style: normal;
  line-height: 1.18;
  font-weight: 600;
  margin: 0 0 24px 0;
}

h3 {
  font-size: 1.56rem;
  line-height: 1.25;
  font-family: Jost, sans-serif;
  font-style: normal;
  font-weight: 600;
  margin: 0 0 24px 0;
}

h4 {
  font-size: 1.11rem;
  font-family: Jost, sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: 1.4;
  margin: 0 0 24px 0;
}

h5 {
  font-size: 1rem;
  font-family: Jost, sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: 1.33;
  margin: 0 0 24px 0;
}

h6 {
  font-size: .89rem;
  font-family: Jost, sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: 1.25;
  margin: 0 0 24px 0;
}

p {
  color: #6a6d77;
  font-family: Jost, sans-serif;
  font-size: 1rem;
  font-style: normal;
  font-weight: 400;
  line-height: 1.56;
  margin: 0 0 12px 0;
}

@media screen and (max-width: 767px) {
  h1 {
    font-size: 1.78rem;
  }

  h2 {
    font-size: 1.56rem;
  }

  h3 {
    font-size: 1.33rem;
  }

  h4 {
    font-size: 1.11rem;
  }

  h5 {
    font-size: 1rem;
  }

  h6 {
    font-size: .89rem;
  }
}


/* Form  */
form {
  background: linear-gradient(111.02deg, #f5693f, #f53f71);
  box-shadow: 0 16px 32px rgba(246, 89, 84, .2), 0 8px 16px rgba(246, 89, 84, .2);
}

input {
  background-color: #fff;
  border: 1px solid #e3e6ec;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(34, 35, 38, .05), 0 4px 8px rgba(34, 35, 38, .05);
  color: #222326;
  display: block;
  font-family: Jost, sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.56;
  padding: .91rem;
  max-width: 100%;
  box-sizing: border-box;
  width: 100%;
}

input:focus {
  outline: none;
}

::placeholder {
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.56;
}

img {
  width: 100%;
  max-width: 100%;
}