/* CitiesTable.css */
.table_banner {
    background: linear-gradient(111.02deg, #f5693f, #f53f71);
    position: relative;
    z-index: 1;
}

.table_banner_text {
    padding: 100px 20px 60px 20px;
    width: 100%;
    max-width: 700px;
    box-sizing: border-box;
    margin: 0 auto;
}

.table_banner h1 {
    color: #ffffff;
}

.banner_svg {
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    position: relative;
    height: 100vw;
    max-height: 292px;
}

@media(max-width:1440px) {
    .banner_svg {
        width: 1440px;
    }
}

.banner_svg svg {
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
}


.box-wrap {
    border-radius: 16px;
    overflow: hidden;
    width: 100%;
    margin-top: -90px;
    position: relative;
    z-index: 1;
    padding-bottom: 100px;
}

.table_content {
    width: 100%;
    max-width: 100%;
    overflow-x: auto;
    height: max-content;
    max-height: 77vh;
    margin: 0 auto;
    border-radius: 16px;
    padding: 0;
    border: 1px solid #e3e6ec;
}

table {
    width: 100%;
    border-collapse: collapse;
    border-radius: 16px 0 16px 0;
    border: 0px solid #e3e6ec;
}

th,
td {
    font-size: 16x;
    /* Increased font size */
    color: #222326;
    text-align: left;
    font-family: "Jost", sans-serif;
    padding: 12px;
    white-space: nowrap;
    border: 1px solid #e3e6ec;
}

th {
    font-size: 18px;
    background-color: #f8f9fb;
    color: #222326;
    border-top: none;
    position: sticky;
    top: 0;
}

th:first-child,
td:first-child {
    min-width: 50px;
    max-width: 75px;
    border-left: none;
    border-right: none;
}

th:last-child,
td:last-child {
    border-right: 1px solid #e3e6ec;
}

th:nth-child(2),
td:nth-child(2),
th:nth-child(4),
td:nth-child(4),
th:nth-child(6),
td:nth-child(6) {
    min-width: 125px;
    max-width: 150px;
}

th:nth-child(3),
td:nth-child(3) {
    min-width: 255px;
    max-width: 315px;
}

th:nth-child(5),
td:nth-child(5),
th:nth-child(7),
td:nth-child(7) {
    min-width: 150px;
    max-width: 175px;
}

tbody tr:nth-child(even) {
    background: #f8f9fb;
    color: #222326;
}

tbody tr:nth-child(odd) {
    background: #ffffff;
    color: #222326;
}

table a {
    position: relative;
    z-index: 1;
}

table a::before {
    position: absolute;
    z-index: -1;
    top: auto;
    bottom: 0;
    content: '';
    width: 0;
    height: 2px;
    background: linear-gradient(111.02deg, #f5693f, #f53f71);
}

table a:hover::before {
    width: 100%;
    opacity: 1;
}

.table_content thead th {
    background-color: #f1f1f1;
    position: sticky;
    top: 0;
    z-index: 2;
}

::-webkit-scrollbar-thumb,
::-webkit-scrollbar-track {
    border-radius: 16px;
}

::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, .05);
}

::-webkit-scrollbar-thumb,
::-webkit-scrollbar-track {
    border-radius: 16px;
}

::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, .1);
}

::-webkit-scrollbar {
    height: 8px;
    width: 8px;
}



.box-wrap {
    position: relative;
    box-sizing: border-box;
}

.loading-progress {
    position: relative;
    width: 100%;
    top: -57vh;
    max-width: 600px;
    border-radius: 10px;
    background: #e3e6ec;
    border: 1px solid #e3e6ec;
    height: 10px;
    left: 0;
    right: 0;
    z-index: 20;
    margin: 0 auto;
}

.progress-bar {
    background: linear-gradient(111.02deg, #f5693f, #f53f71);
    position: relative;
    height: 10px;
    border-radius: 10px;
    z-index: 35;
}


@media(max-width:1440px) {
    .table_banner_text {
        padding-top: 60px;
    }
}