.weather_image_container {
    position: relative;
    z-index: 1;
    height: 100vw;
    max-height: 590px;
}

.weather_image_container::before {
    position: absolute;
    z-index: -1;
    content: '';
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 83%;
    height: 100vw;
    max-height: 590px;
    background-image: url(/src/Components/Images/Weather.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    border-radius: 24px;
}

.icon_container {
    padding-bottom: 15px;
}

.weather_image_container::after {
    position: absolute;
    z-index: -1;
    content: '';
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 83%;
    height: 100vw;
    max-height: 590px;
    background-color: #222326;
    opacity: 0.5;
    border-radius: 24px;
}

.current_weather {
    position: absolute;
    width: 40%;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    box-shadow: 0 16px 32px rgba(34, 35, 38, .05), 0 8px 16px rgba(34, 35, 38, .05);
    padding: 48px;
    background: #ffffff;
    z-index: 1;
    box-sizing: border-box;
    border-radius: 24px;
}

.weather_container {
    position: relative;
    z-index: 1;
    margin-top: -90px;
    text-align: left;
}

.weather_section .table_banner_text {
    padding-bottom: 18px;
}

.weather_section p {
    text-align: left;
    text-transform: capitalize;
}

.weather_section p span.heading_text {
    font-weight: 600;
}

.next_predection {
    padding: 100px 0;
}

.next_predict_cards {
    gap: 24px;
    padding: 60px 0 0 0;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}

.next_predict_cards div {
    box-shadow: 0 16px 32px rgba(34, 35, 38, .05), 0 8px 16px rgba(34, 35, 38, .05);
    padding: 24px;
    background: #ffffff;
    z-index: 1;
    box-sizing: border-box;
    border-radius: 24px;
}

.current_weather h3 {
    margin-bottom: 30px;
}

.border_heading h1,
.border_heading h2,
.border_heading h3,
.border_heading h4,
.border_heading h5,
.border_heading h6 {
    position: relative;
    z-index: 1;
    display: inline-block;
}

.border_heading h1::before,
.border_heading h2::before,
.border_heading h3::before,
.border_heading h4::before,
.border_heading h5::before,
.border_heading h6::before {
    position: absolute;
    content: '';
    z-index: -1;
    left: 0;
    right: 0;
    top: 120%;
    background: linear-gradient(111.02deg, #f5693f, #f53f71);
    border-radius: 4px;
    height: 4px;
    max-width: 80px;
    width: 100%;
    margin: 0 auto;

}

@media screen and (max-width: 1024px) and (min-width:768px) {
    .next_predict_cards {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media screen and (max-width: 767px) {
    .current_weather {
        position: relative;
        width: 100%;
        max-width: 90%;
        margin: 0 auto;
        top: 100%;
        padding: 24px;
        transform: translateY(-15%);
    }

    .weather_image_container,
    .weather_image_container::after,
    .weather_image_container::before {
        width: 100%;
        height: 300px;
    }

    .next_predict_cards {
        grid-template-columns: repeat(1, 1fr);
    }
}